@font-face {
	font-family: "wticons";
	src: url("data:font/woff2;charset=utf-8;base64,") format("woff2");
}

.wticons {
	line-height: 1;
}

.wticons:before {
	font-family: wticons !important;
	font-style: normal;
	font-weight: normal !important;
	vertical-align: top;
}


.no-gutters{
    box-shadow: 0 10px 20px rgb(0 0 0 / 15%);
}

.card-form__body{
    background-color: #f6f6f6;
}



/* .card-body{
    background-color: white;
} */

.form-group{
    margin-top : 20px;
    margin-bottom: 20px;
}

.media{
    display: flex;
}
.mr-3 {
    margin-right: 1rem!important;
}

.align-items-center {
    align-items: center!important;
}

.w-auto {
    width: auto!important;
}

.custom-select {
    transition: background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

.custom-select {
    display: inline-block;
    width: 100%;
    height: calc(1.5em + 0.75rem + 2px);
    padding: 0.375rem 1.875rem 0.375rem 0.875rem;
    font-size: .925rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    vertical-align: middle;
    background: #fff url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='4' height='5'%3E%3Cpath fill='%23343a40' d='M2 0L0 2h4zm0 5L0 3h4z'/%3E%3C/svg%3E") no-repeat right 0.875rem center/8px 10px;
    border: 1px solid #efefef;
    border-radius: 0.25rem;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

.text-right {
    text-align: right
}

.mb-5 {
    margin-bottom: 3rem!important;
}

.avatar-lg {
    font-size: 1.33333rem;
    width: 4rem;
    height: 4rem;
}

.avatar-img {
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
    object-fit: cover;
}