@media (max-width: 767px){
.job-post-company {
    padding-top: 70px;
    padding-bottom: 70px;
}}

div {
    display: block;
}

.container {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

.body-box{
    margin-top: 20px;
}

.justify-content-between {
    justify-content: space-between;
}

.row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
}

.col-12, .col-lg-10, .col-lg-12, .col-lg-2, .col-lg-3, .col-lg-4, .col-lg-6, .col-lg-8, .col-lg-9, .col-md-10, .col-md-2, .col-md-3, .col-md-4, .col-md-6, .col-md-8, .col-md-9, .col-sm-4, .col-sm-5, .col-sm-6, .col-xl-10, .col-xl-12, .col-xl-2, .col-xl-3, .col-xl-4, .col-xl-6, .col-xl-7, .col-xl-8, .col-xl-9 {
    position: relative;
    width: 100%;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
}

.single-job-items {
    padding: 36px 30px;
    flex-wrap: wrap;
    -webkit-transition: .4s;
    -moz-transition: .4s;
    -o-transition: .4s;
    transition: .4s;
}

.single-job-items .job-items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

@media (max-width: 767px){
.single-job-items .job-tittle {
    padding-top: 25px;
}}

@media (max-width: 767px){
.single-job-items .job-tittle ul li {
    margin-right: 0px;
    margin-bottom: 7px;
}}

.job-tittle ul{
    padding:0;
}

.single-job-items .job-tittle ul li {
    display: inline-block;
    margin-right: 48px;
    font-size: 15px;
    color: #808080;
    line-height: 1.8;
}



*, ::after, ::before {
    box-sizing: border-box;
}
.single-job-items .job-tittle a h4 {
    color: #28395a;
    font-size: 24px;
    -webkit-transition: .4s;
    -moz-transition: .4s;
    -o-transition: .4s;
    transition: .4s;
}

.single-job-items .job-tittle a{
    text-decoration: none;
}

.single-job-items .job-tittle a h4:hover{
    color:#1b59d6;
}

*, ::after, ::before {
    box-sizing: border-box;
}

.mb-50 {
    margin-bottom: 50px;
}

.post-details2 ul {
    padding-left: 30px;
}

ul {
    margin: 0px;
    padding: 0px;
}

ul {
    display: block;
    list-style-type: disc;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 40px;
}

.post-details3 {
    padding: 30px 30px 30px 30px;
    border: 1px solid #ededed;
}

.post-details3 ul li {
    display: flex;
    justify-content: space-between;
    margin-bottom: 12px;
}
.post-details3 ul{
    padding:0;
}

.post-details2 ul li {
    list-style-type: circle;
}

.post-details4 ul{
    padding:0;
}

.post-details4 ul li span {
    color: #5b5c6e;
    font-weight: 400;
    font-size: 16px;
    display: inline-block;
    padding-left: 18px;
}

li {
    list-style: none;
}


.apply-btn{
    color:white;
    background-color:#fb246a;
    padding:10px;
}

.cname{
    color: #1b59d6;
    font-size: 20px;
}

.div-card{
    box-shadow: 0 10px 20px rgb(0 0 0 / 5%);
    border: 1px solid transparent;
    padding: 30px 20px 25px 20px;
    transition: all .3s ease-out 0s;
    border-radius: 5px;
}

.details-card{
    transition: transform .3s;
    box-shadow: 0 10px 20px rgb(0 0 0 / 5%);
    border: 1px solid transparent;
    padding: 30px 20px 25px 20px;
    transition: all .3s ease-out 0s;
    border-radius: 5px;
}

.details-card:hover{
    -ms-transform: scale(1.03); /* IE 9 */
    -webkit-transform: scale(1.03); /* Safari 3-8 */
    transform: scale(1.03); 
    border: 1px solid #007aff;
}

.company-logo{
    height:80px;
    width:100px;
}

.job-items{
    display:flex;
    flex-direction: column;
    justify-content: center;
}